<template>
  <div class="page-footer">
    <div class="footer-content">
      <div class="company-name"></div>
      <div class="com-disc"></div>
      <div class="text-line">
        <span>112</span>
      </div>
      <div class="text-line">
        <span>400-8610-855</span>
      </div>
      <div class="text-line">
        <span>hancui@hancfl.com</span>
      </div>
      <div class="text-line">
        <span>www.hancfl.com</span>
      </div>
      <div class="code-area">
        <div class="code-cont">
          <div class="code code1"></div>
          <div class="code code2"></div>
        </div>
        <div class="text"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'PageFooter'
  }
</script>

<style lang="scss" scoped>
  .page-footer {
    background: #069163;
    .footer-content {
      width: 12rem;
      margin: auto;
      position: relative;
      .company-name {
        font-size: 0.18rem;
        color: #FFFFFF;
        padding-top: 0.67rem;
      }
      .com-disc {
        font-size: 0.14rem;
        color: #FFFFFF;
        margin-top: 0.21rem;
        margin-bottom: 0.71rem;
      }
      .text-line {
        color: #FFFFFF;
        padding-bottom: 0.56rem;
        span {
          font-size: 0.18rem;
        }
      }
      .code-area {
        position: absolute;
        top: 1.93rem;
        right: 0;
        .code-cont {
          display: flex;
          .code {
            width: 1.44rem;
            height: 1.38rem;
            background-size: 100% 100%;
            margin-left: 0.43rem;
          }
          .code1 {
            background-image: url("~@IMG/qcode1.png");
          }
          .code2 {
            background-image: url("~@IMG/qcode2.png");
          }
        }
        .text {
          font-size: 0.18rem;
          color: #FFFFFF;
          margin-top: 0.28rem;
          text-align: center;
        }
      }
    }
  }
</style>



