<template>
  <div class="page-content Products-Detail">
    <div class="page-inner">
      <div class="Breadcrumb">
        <span></span>/<span></span>/<span>{{ brandName }}</span>
      </div>
      <div class="pro-des">
        <div class="pro-show">
          <!---->
          <div class="Stand-up-swiper">
            <div class="pro-img-smail"
                 v-for="item in ProductDetail.imgs"
                 :key="item"
                 :style="{ backgroundImage: 'url(' + item + ')' }"
            ></div>
          </div>
          <!---->
          <div class="pro-img"
               :style="{ backgroundImage: 'url(' + ProductDetail.logo + ')' }"
          ></div>
          <!---->
          <div class="pro-msg">
            <div class="title">{{ ProductDetail.title }}/<span class="English">Rose Hydrosol</span></div>
            <div class="desc">{{ ProductDetail.sub_title }}</div>
            <div class="price">
              <span class="size">500mL</span>
              <span class="num">{{ ProductDetail.origin_price }}</span>
            </div>
            <div class="to-buy-btn"></div>
            <div class="line"></div>
            <div class="work-title"></div>
            <div class="work-des">{{ ProductDetail.effect }}</div>
          </div>
        </div>
      </div>
      <div class="use-text">
        <div class="block">
          <div class="title"></div>
          <div class="desc">{{ ProductDetail.apply_scope }}</div>
        </div>
        <div class="block">
          <div class="title"></div>
          <div class="desc">{{ ProductDetail.nuclear }}</div>
        </div>
        <div class="block">
          <div class="title"></div>
          <div class="desc">{{ ProductDetail.usage_method }}</div>
        </div>
      </div>
      <!---->
      <div class="more-pro-area">
        <div class="area-title"></div>
        <div class="english">More products</div>
      </div>
      <!---->
      <div class="page-line"></div>
    </div>
    <div class="pro-swiper-area">
      <swiper class="swiper" :options="swiperOption">
        <swiper-slide
          class="pro-area"
          v-for="item in ProductList"
          :key="item.id"
        >
          <div class="pro-image"
               :style="{ backgroundImage: 'url(' + item.logo + ')' }"
          ></div>
          <div class="title">{{ item.title }}</div>
          <div class="sub_title">{{ item.sub_title }}</div>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
  import {
    productDetailApi,
    productListApi
  } from '@/apis/index'

  export default {
    name: 'ProductsDetail',
    data() {
      return {
        swiperOption: {
          slidesPerView: 'auto',
          autoplay: true
        },
        ProductDetail: {},
        ProductList: [],
        proType: 1
      }
    },
    mounted() {
      this.getProductDetail()
      this.getProductList()
    },
    computed: {
      brandName() {
        if (this.proType === 1) {
          return ''
        } else if (this.proType === 2) {
          return ''
        } else {
          return ''
        }
      }
    },
    methods: {
      getProductDetail() {
        const params = {
          id: Number(this.$route.params.id)
        }
        productDetailApi(params).then(res => {
          console.log(res)
          this.ProductDetail = JSON.parse(JSON.stringify(res.result))
          this.proType = Number(this.ProductDetail.type)
        })
      },
      getProductList() {
        const params = {
          type_id: this.proType,
          page: 1,
          size: 100,
          keyword: ''
        }
        productListApi(params).then(res => {
          this.ProductList = JSON.parse(JSON.stringify(res.result))
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .page-inner {
    padding-top: 0.29rem;
    .pro-des {
      padding-top: 0.96rem;
      .pro-show {
        height: 5.73rem;
        display: flex;
        align-items: center;
        .Stand-up-swiper {
          width: 1.5rem;
          height: 100%;
          margin-right: 0.16rem;
          .pro-img-smail {
            width: 1.5rem;
            height: 1.5rem;
            margin-bottom: 0.16rem;
            background-size: 100% 100%;
          }
        }
        .pro-img {
          width: 5.17rem;
          height: 100%;
          background-size: 100% 100%;
        }
        .pro-msg {
          width: 4.65rem;
          height: 100%;
          padding-left: 0.51rem;
          color: #333333;
          .title {
            font-size: 0.3rem;
            padding-top: 0.2rem;
            .English {
              font-size: 0.16rem;
            }
          }
          .desc {
            font-size: 0.18rem;
            padding: 0.18rem 0 0.5rem;
            line-height: 1.4;
          }
          .price {
            width: 100%;
            height: 0.6rem;
            border-radius: 0.05rem;
            border: 0.01rem solid #333333;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 0.13rem 0 0.05rem;
            span {
              font-size: 0.16rem;
              font-weight: 300;
            }
          }
          .to-buy-btn {
            margin-top: 0.5rem;
            width: 100%;
            height: 0.6rem;
            background: #069062;
            box-shadow: 0 0.03rem 0.04rem 0 rgba(0, 54, 36, 0.39);
            border-radius: 0.05rem;
            font-size: 0.24rem;
            color: #FFFFFF;
            text-align: center;
            line-height: 0.6rem;
            font-weight: 400;
            cursor: pointer;
          }
          .line {
            width: 100%;
            border-bottom: 0.01rem dotted #666666;
            margin-top: 0.7rem;
          }
          .work-title {
            width: 100%;
            padding: 0.3rem 0;
            font-size: 0.24rem;
            color: #069163;
          }
          .work-des {
            width: 100%;
            font-size: 0.18rem;
            color: #333333;
            line-height: 1.4;
          }
        }
      }
    }
    .use-text {
      display: flex;
      padding-top: 1rem;
      .block {
        width: 33.33%;
        .title {
          font-size: 0.24rem;
          color: #069163;
          text-align: center;
          padding-bottom: 0.32rem;
        }
        .desc {
          font-size: 0.16rem;
          color: #666666;
          text-align: center;
          padding: 0 0.68rem;
          height: 1.6rem;
          margin: auto;
          line-height: 1.4;
          border-right: 0.01rem dotted #333333;
        }
        &:last-child {
          .desc {
            border: none;
          }
        }
      }
    }
    .more-pro-area {
      padding-top: 0.32rem;
      text-align: center;
      .area-title {
        font-size: 0.24rem;
        color: #068F61;
      }
      .english {
        font-size: 0.12rem;
        color: #069163;
        padding-top: 0.11rem;
        padding-bottom: 0.41rem;
      }
    }
  }
  .pro-swiper-area {
    padding: 0.42rem 0 0.5rem;
    overflow: hidden;
    /deep/ .swiper-container {
      overflow: visible;
    }
    .pro-area {
      width: 2.7rem;
      height: 3.4rem;
      background-color: #FFFFFF;
      cursor: pointer;
      margin-right: 0.3rem;
      font-size: 16px;
      box-shadow: 0 0 0.21rem 0 rgba(0, 64, 51, 0.15);
      padding-top: 0.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      &:hover {
        transform: scale(1.11, 1.11);
        transition: transform .2s;
      }
      .pro-image {
        width: 2.2rem;
        height: 2.2rem;
        background-size: 100% 100%;
      }
      .title {
        text-align: center;
        font-size: 0.16rem;
        color: #333333;
        padding: 0.16rem 0.1rem 0;
      }
      .sub_title {
        color: #333333;
        font-size: 0.12rem;
        text-align: center;
        padding: 0.09rem 0.1rem 0;
        line-height: 1.4;
      }
    }
  }
</style>



